        <div class="close-btn" @click="cancelBtn">×</div>
      </div>
      <div class="content">
        <div class="topLeft"></div>
        <div class="topRight"></div>
        <div class="buttomRight"></div>
        <div class="buttomLeft"></div>
        <div class="withinBox">
          <div class="title">{{title}}</div>
          <div class="text-conten">{{content}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CloseBtn from "@/components/CloseBtn";
export default {
  components: {
    CloseBtn,
  },
  data() {
    return {
      title: "",
      content: "",
    };
  },
  created() {
    this.title = this.$route.query.details.title;
    this.content = this.$route.query.details.content;
  },
  watch: {
    $route(newVla) {
      this.title = newVla.query.details.title;
      this.content = newVla.query.details.content;
    },
  },
  methods: {
    cancelBtn() {
      this.$router.push({ name: "Home" });
    },
  },
};
</script>

